
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import axios from 'axios'
import { userInfoTran } from '../../utils/dataTran'
export default {
components: {
XHeader,
Search,
Checklist
},
data () {
return {
results: [],
value: '',
chooseItem: '',
list: [],
chooseIds: []
}
},
methods: {
change (value) {
this.chooseIds = value
},
getResult (val) {
axios
.get(`/api/getfriendslist`, {
params: {
userId: global.user.id,
val: val || ''
}
})
.then((res) => {
const data = res.data.status && userInfoTran(res.data.list, false)
data.forEach((el, index) => {
this.list.push({
key: el.id,
value: el.name
})
})
}).catch((error) => {
console.log(error)
})
}
},
created () {
this.getResult()
}
}



