
import AlertModule from 'vux/src/plugins/alert/module'
import Search from 'vux/src/components/search/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'

import axios from 'axios'
import {fileInfoTran} from '../../utils/dataTran'
export default {
components: {
Search,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Tab,
TabItem,
Confirm,
AlertModule,
PopupHeader,
Popup,
Radio,
Group
},
data () {
return {
results: [],
value: '',
list: [],
index01: 0,
show: false,
currentIndex: 0,
confirmCont: '',
currentTabIndex: 0,
show1: false,
options: [{
key: 0,
value: ''
}],
shareType: ''
}
},

mounted () {
console.log(123123)
this.searchWorkFiles(0, 0)
},
methods: {
resultClick (item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult (val) {
console.log('on-change', val)
this.results = val ? this.getResult(this.value) : []
},
change (value, label) {
this.shareType = value
},
dele () {
const body = {
userId: global.user.id,
fileId: this.list[this.currentIndex].id
}
axios.post(`/api/deletefile`, body)
.then((res) => {
AlertModule.show({
title: '',
content: this.list[this.currentIndex].isTrash ? '' : ''
})
this.searchWorkFiles(this.currentTabIndex, this.currentTabIndex === 2 ? 1 : 0)
}).catch((error) => {
console.log(error)
})
},
share () {

const re = /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z0-9]+$/
if (this.shareType === 0) {
this.$router.push({path: '/mine/choosefriend'})
} else if (re.test(this.shareType) === true) {
console.log('')
this.readySendMail()
} else {
AlertModule.show({
title: ''
})
this.show1 = true
}
},
readySendMail () {

axios
.post(`/api/sendmail`, {
from: 'userName',
to: 'haowen737@gmail.com',
content: 'hi, zhengmeili'
})
.then((res) => {
this.$vux.toast.text('')
this.show1 = false
}).catch((error) => {
this.$vux.toast.text(error || '')
})
},
revert (index) {
const body = {
userId: global.user.id,
fileId: this.list[this.currentIndex].id
}
axios.post(`/api/revertfile`, body)
.then((res) => {
this.searchWorkFiles(this.currentTabIndex, this.currentTabIndex === 2 ? 1 : 0)
}).catch((error) => {
console.log(error)
})
},
onSubmit () {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
switchTabitem (index) {
this.currentTabIndex = index
switch (index) {
case 0: this.searchWorkFiles(0, 0)
break
case 1: this.searchWorkFiles(1, 0)
break
case 2: this.searchWorkFiles(2, 1)
break
default: this.searchWorkFiles(0, 0)
break
}
},
jumpToEdit () {
const data = this.list[this.currentIndex]
let name = ''
switch (data.fileType) {
case 'md': name = 'noteAddMakdown'
break
case 'html': name = '/note/add/input'
break
default: name = 'noteAddMakdown'
break
}
const opt = {
name,
params: {
item: data
}
}
this.$router.push(opt)
},
searchWorkFiles (type, isTrash) {
const userId = window.localStorage.getItem('userId')
axios.get(`/api/getfilelist`, {
params: {

id: userId,
type,
isTrash
}})
.then((res) => {
if (res.data.status) {
this.list = fileInfoTran(res.data.list)
} else {
AlertModule.show({
content: res.data.msg
})
}
}).catch((error) => {
console.log(error)
})
}
}
}



