
import Icon from 'vue-awesome/components/Icon'
import 'font-awesome/css/font-awesome.min.css'
import axios from 'axios'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Search from 'vux/src/components/search/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import { userInfoTran } from '../../utils/dataTran'















export default {
components: {
Icon,
Group,
Cell,
CellBox,
Search,
XDialog,
XHeader
},
methods: {
add (index) {
const body = {
userId: global.user.id,
friendId: this.list[index].id
}
const _this = this
axios
.post(`/api/addfriend`, body)
.then((res) => {
if (res.data) {
AlertModule.show({
title: '',
onHide () {
_this.$router.push({path: '/mine/friends'})
}
})
}
}).catch((error) => {
console.log(error)
})
},
scan () {
this.$router.push('/mine/qrcode/scanner')
},
getResult (val) {
val !== '' && axios
.get(`/api/getuserlist`, {
params: {
userId: global.user.id,
val: val || ''
}
})
.then((res) => {
this.list = res.data.status && userInfoTran(res.data.list, false)
}).catch((error) => {
console.log(error)
})
}
},
data () {
return {
list: []
}
}
}



